.App {
  text-align: center;
}
html, body, #root{
  background-color: black;
  color: white;
  height: 100%;
  width: 100%;
  margin: 0;
}

#drum-machine{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
  border: 1px solid red;
}
#display{
  border: 5px dotted green;
  padding-left: 10px;
  padding-right: 10px;
}


.drumpad{
  display: inline-block;
  width: 20px;
  margin: 5px 5px 5px 5px;
  padding: 30px;
  border: 1px solid white;
}
